<template>
	<uni-drawer ref="showLeft" mode="left" :width="200" @change="change($event,'showLeft')">
		 <image style="width: 200px; height: 200px; background-color: #eeeeee;" :mode="'scaleToFill'" :src= "'https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/shuijiao.jpg'" ></image>
		<uni-list :border="false">
			<uni-list-item :border="false" :show-extra-icon="true" :extra-icon="chatboxes" title="Friends"></uni-list-item>
			<uni-list-item :border="false" :show-extra-icon="true" :extra-icon="eye" title="Statistics"></uni-list-item>
			<uni-list-item :border="false" :show-extra-icon="true" :extra-icon="gear" title="Settings"></uni-list-item>
		</uni-list>
	</uni-drawer>
</template>
<script>
	import uniDrawer from "@/components/uni-drawer/uni-drawer.vue"
	import uniIcons from "@/components/uni-icons/uni-icons.vue"
	export default {
		components: {
			uniDrawer,
			uniIcons
		},
		data() {
			return {
				showLeft: false,
				chatboxes: {
					color: '#93989d',
					size: '22',
					type: 'chatboxes'
				},
				eye: {
					color: '#93989d',
					size: '22',
					type: 'eye'
				},
				gear: {
					color: '#93989d',
					size: '22',
					type: 'gear'
				}
			}
		},
		methods: {
			confirm() {},
			// 打开窗口
			showDrawer(e) {
				this.$refs[e].open()
			},
			// 关闭窗口
			closeDrawer(e) {
				this.$refs[e].close()
			},
			// 抽屉状态发生变化触发
			change(e, type) {
				console.log((type === 'showLeft' ? '左窗口' : '右窗口') + (e ? '打开' : '关闭'));
				this[type] = e
			}
		},
	}
</script>

<style>
	page {
		display: flex;
		flex-direction: column;
		box-sizing: border-box;
		background-color: #efeff4;
		min-height: 100%;
		height: auto;
	}

	view {
		font-size: 14px;
		line-height: inherit;
	}

	.content {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}
</style>
